export default {
	data() {
		return {
			providerList: []
		}
	},
	mounted() {
		uni.getProvider({
		    service: 'oauth',
		    success: (result) => {
		        this.providerList = result.provider.map((value) => {
		            let providerName = '';
					let icon = '';
					let bgColor = ''
		            switch (value) {
		                case 'weixin':
		                    providerName = '微信登录'
							icon = 'iconweixin'
							bgColor = 'bg-success'
		                    break;
		                case 'qq':
		                    providerName = 'QQ登录'
							icon = 'iconqq'
							bgColor = 'bg-primary'
		                    break;
		                case 'sinaweibo':
		                    providerName = '新浪微博登录'
							icon = 'iconweibo'
							bgColor = 'bg-warning'
		                    break;
		            }
		            return {
		                name: providerName,
		                id: value,
						icon: icon,
						bgColor: bgColor
		            }
		        });
		
		    },
		    fail: (error) => {
		        console.log('获取登录通道失败', error);
		    }
		});
	}
}
